import React, {useEffect, useState} from "react";
import {Table} from "../../../../components/tables/Table";
import {useTranslation} from "react-i18next";
import styles from "./viewWorkItem.module.scss";
import {Icons, UserList} from "../../../../components";
import {ICONS} from "../../../../utils/iconNames";
import _ from "lodash";
import {DATE_FORMAT, DATE_TIME_FORMAT} from "../../../../utils/common";
import i18n from "../../../../i18n";
import moment from "moment";
import {phoneNumberMasking} from "../../../../utils/commonFunctions";
import {useSelector} from "react-redux";

export const ViewWorkTable=props=>{
    const {workObject,viewChoiceListData}=useSelector((state) => state.workSearch);
    const {t} = useTranslation();
    const [tableData,setTableData]=useState([]);
    const [tableColumnNames, setTableColumnNames] = useState([
        {name: 'name', caption: t('label.gridLayout.Name'), colWidth: '150px'},
        {name: 'objectValue', caption:t('sysmgr.clientintegration.value.title'), colWidth:'100%'}]);

    useEffect(()=>{
        if(workObject?.fieldDefinitions?.length){
            let ddd = workObject?.fields?.map(t1=>({...t1, ...workObject?.fieldDefinitions?.find(t2=> t2.name===t1.name)}));
            let filteredData= ddd.filter(f=>f.isVisible)
            let criteriaDefinitions = _.map(filteredData, (data, index) => {
                return {
                    ...data,
                    name:data.isRequired ? <div style={{display:'flex',flexDirection:'row'}}>
                        <div className={styles.requiredText}>{'*'}</div>{data.caption}</div>
                        :<div>{data.caption}</div>,
                    objectValue: createInteractiveCell(data, index),
                }
            });
            setTableData(criteriaDefinitions);
        }
    },[workObject?.fieldDefinitions,viewChoiceListData]);

    const createInteractiveCell=(data,index)=>{
        const isViewOnly=data.isLocked ?? false ;
        let sourceCell = null
        let source = data.objectValue;
        let selectionListData = viewChoiceListData.find(t1 => t1.name === data.choiceListName)?.selections;
        switch (data.viewType) {
            case -1:
                sourceCell =  <div>{source}</div>;
                break;
            case 'BOOLEAN_TEXT_BOX':
                sourceCell = <div>{'BOOLEAN_TEXT_BOX'}</div>;
                break;
            case 1 ://'STRING_TEXT_BOX'
                sourceCell = <div className={styles.sourcePlaceHolder}>
                        <div className={styles.sourceText}>{source}</div>
                        {isViewOnly ? data.operator === 'IN' || data.operator === 'IS NOT IN' ?
                            <Icons icon={ICONS.FORM_CHECKBOX}/> :
                            <Icons icon={ICONS.FORM_TEXT}/>:null}
                    </div>
                break;
            case 4: //INTEGER_TEXT_BOX
                    sourceCell = <div className={styles.sourcePlaceHolder}>
                        <div className={styles.sourceText}>{source}</div>
                        {isViewOnly ?data.operator === 'IN' || data.operator === 'IS NOT IN' ?
                            <Icons icon={ICONS.FORM_CHECKBOX}/> :
                            <Icons icon={ICONS.FORM_TEXT_HASH}/>:null}
                    </div>
                break;
            case 3: //'NUMBER_TEXT_BOX'
                console.log('**s',source)
                    sourceCell = <div className={styles.sourcePlaceHolder}>
                        <div className={styles.sourceText}>{i18n?.language.includes('fr') ?parseFloat(source).toFixed(2).replace('.',','):parseFloat(source).toFixed(2)}</div>
                        {isViewOnly ?data.operator === 'IN' || data.operator === 'IS NOT IN' ?
                            <Icons icon={ICONS.FORM_CHECKBOX}/> :
                            <Icons icon={ICONS.FORM_TEXT_HASH}/>:null}
                    </div>
                break;
            case 5://DATETIME_TEXT_BOX
               if (data.operator === 'ON') {
                    sourceCell = <div className={styles.sourcePlaceHolder}>
                        <div
                            className={styles.sourceText}>{ source && moment(source).format( DATE_FORMAT )}</div>
                        {isViewOnly ?<Icons icon={ICONS.FORM_CLOCK}/>:null}
                    </div>;
                } else {
                    sourceCell = <div className={styles.sourcePlaceHolder}>
                        <div className={styles.sourceText}>{source && moment(source).format(DATE_TIME_FORMAT)}</div>
                        {isViewOnly ?<Icons icon={ICONS.FORM_CLOCK}/>:null}
                    </div>;
                }
                break;
            case 6: //'DATE_TEXT_BOX'
                    sourceCell = <div className={styles.sourcePlaceHolder}>
                        <div className={styles.sourceText}>{source && moment(source).format(DATE_FORMAT)}</div>
                        {isViewOnly ?<Icons icon={ICONS.FORM_CALENDER}/>:null}
                    </div>;
                break;
            case 7: //MEMO
                sourceCell = <div className={styles.sourcePlaceHolder}>
                    <div className={styles.sourceText1}>{source}</div>
                    {isViewOnly ?<Icons icon={ICONS.FORM_SCROLL}/>:null}
                </div>
                break;
            case 8 ://'COMBO_BOX'
                    sourceCell = <div className={styles.sourcePlaceHolder}>
                        <div className={styles.sourceText}>
                            {selectionListData?.find(t1 => t1.name === source ||  t1.name === data.value)?.description}
                        </div>
                        {isViewOnly ?data.operator === 'IN' || data.operator === 'IS NOT IN' ? <Icons icon={ICONS.FORM_CHECKBOX}/> :
                            <Icons icon={ICONS.FORM_LIST_ARROW}/>:null}
                    </div>
                break;
            case 'BOOLEAN_CHECKBOX':  // Pending
                sourceCell = <div>{'BOOLEAN_CHECKBOX'}</div>;
                break;
            case 'MULTISELECT':
                sourceCell = <div className={styles.sourcePlaceHolder}>
                    <div className={styles.sourceText}>{source}</div>
                    {isViewOnly ?<Icons icon={ICONS.FORM_LIST_ARROW}/>:null}
                </div>
                break;
            case 11 ://'USERSELECT'
                sourceCell= <UserList
                    isShowIcon={isViewOnly}
                    selectedSource={source}
                    selectedValue={source}
                />
                break;
            case 12: //'PHONENUMBER_TEXTBOX'
                    sourceCell =  <div className={styles.sourcePlaceHolder}>
                        <div className={styles.sourceText}>{phoneNumberMasking(source)}</div>
                        {isViewOnly ?data.operator === 'IN' || data.operator === 'IS NOT IN' ?
                            <Icons icon={ICONS.FORM_CHECKBOX}/> :
                            <Icons icon={ICONS.FORM_PHONE}/>:null}
                    </div>
                break;
            case 14: //BOOLEAN_COMBO_BOX
                sourceCell = <div className={styles.sourcePlaceHolder}>
                    <div className={styles.sourceText}>{source == '0' ? t('com.ipd.common.boolean.false') : source == '1' ? t('com.ipd.common.boolean.true') : ''}</div>
                    {isViewOnly ?<Icons icon={ICONS.FORM_TRUE_FALSE}/>:null}
                </div>
                break;
            case 'ROLE_MULTI_SELECT':  // Pending
                sourceCell =  <div className={styles.sourcePlaceHolder}>
                    <div className={styles.sourceText}>{source}</div>
                    {isViewOnly ?<Icons icon={ICONS.FORM_LIST_ARROW}/>:null}
                </div>
                break;
            default:
                return <div/>
        }
        return  !isViewOnly ? <div className={styles.rowTextStyle} style={{maxHeight:'50px', overflow:'auto'}}>
            <div className={styles.sourcePlaceHolder}>
                <div className={styles.sourceText}>{sourceCell}</div>
                <Icons icon={ICONS.FORM_BLOCK}/>
            </div>
        </div>: sourceCell
    }

    return <div style={{overflow:'hidden'}}>
        {workObject?.workType && <div className={styles.processHeader}>
            <Icons icon={ICONS.WORK_EDIT}/><h4 style={{paddingLeft:'5px'}}>{workObject?.workType}</h4>
        </div>}
        <Table
            tableData={tableData}
            searchResultCol={tableColumnNames}
            disabledColName={['name']}
            tableName={t('case.reassign.details.props.label')}
            tableId={t('case.reassign.details.props.label')}
            isFullWidth={true}
            setTableColumnsWidth={setTableColumnNames}
        />
    </div>
}