import React, {useEffect, useState} from 'react';
import { Collapse, H5} from "@blueprintjs/core";
import {Icons} from "../../../components";
import { ICONS} from "../../../utils/iconNames";
import styles from './leftPanel.module.scss';
import { useHistory } from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {setActiveTab, clearContextData} from "../../../slices/dashboardSlice";
import {getTemplate} from "../../../api";
import {useLocation} from "react-router";

export const LeftPanel = ({data, subData,onSubDataClick},...props) => {
    const history = useHistory();
    const dispatch = useDispatch();
    const location = useLocation();
    const [showLoading, setShowLoading] = useState(false);
    const [loading, setLoading] = useState(false);
    const [loadingIndex, setLoadingIndex ] = useState();
    const {activeTabIndex, activeSubTabIndex} = useSelector((state) => state.dashboard)

    const handleClick = (selectedSearch, route) => {
        setLoading(true);
        if(activeTabIndex!==selectedSearch) {
            dispatch(clearContextData([]));
            dispatch(setActiveTab(selectedSearch));
            history.replace(route);
        }else{
            handleSubTabClick(0, null, route);
            (subData[0]?.templateId ?? subData[0]?.id ) && getTemplateDetails( subData[0]?.templateId ?? subData[0]?.id);
        }
        setTimeout(()=> {
            setLoading(false);
        }, 1000)
    };

    const getTemplateDetails=(templateId)=>{
        let contextName=''
        let searchContextName=''
        switch (location.pathname.toString()) {
            case '/':
                contextName='';
                searchContextName=''
                break;
            case '/work-search':
                contextName='searchcontext';
                searchContextName='process'
                break;
            case '/case-search':
                contextName='casesearch';
                searchContextName='cases'
                break;
            case '/content-search':
                contextName='contentsearch';
                searchContextName='content'
                break;
            case '/history-search':
                contextName='historysearch';
                searchContextName='history'
                break;
            default:
        }
        getTemplate(contextName, templateId ,searchContextName);
    }

    const handleSubTabClick = (index, tabDetail,route=null) => {
        (tabDetail?.templateId ?? tabDetail?.id ) && getTemplateDetails(tabDetail?.templateId ?? tabDetail?.id);
        if(onSubDataClick){
            setShowLoading(true);
            setLoadingIndex(index);
            setTimeout(()=>{
                setShowLoading(false);
                onSubDataClick(index,route)
            }, 1000);
        }
    };

    return (
        <div className={styles.leftPanel}>
            {data?.map((tab,index)=>{
                return <div key={index} className={activeTabIndex === index ? styles.containerStyle : null}>
                    <div className={ activeTabIndex === index ? styles.selectedTabContainerStyle : styles.tabContainerStyle}
                         onClick={()=> handleClick(index, tab.route)}>
                        <div className={styles.headerContainerStyle}>
                            <div className={styles.headerIconTitleContainerStyle}>
                                <div><Icons icon={tab.icon}/></div>
                                <H5 className={activeTabIndex === index? styles.headerTextStyle :styles.selectedHeaderTextStyle}>
                                    {tab.title}
                                </H5>
                            </div>
                            <div className={styles.loadingIconContainerStyle}>
                                {loading && activeTabIndex === index && <div className={styles.loadingGifStyle}/>}
                            </div>
                        </div>
                    </div>
                    <div className={styles.collapsableContainerStyle}>
                    <Collapse className={activeTabIndex === index? styles.collapsableActivePanel: styles.collapsablePanel} isOpen={activeTabIndex === index}>
                        <div className={styles.collapsableListContainer}>
                            {subData.map((detail, index) => {
                                return <div key={index}
                                            className={activeSubTabIndex === index ? styles.selectedDetailsContainerStyle : styles.detailsContainerStyle}
                                            onClick={() => handleSubTabClick(index, detail)}>
                                    {showLoading && loadingIndex === index && !loading ?
                                        <div className={styles.loadingGifStyle}/> :
                                        <div className={styles.subDataParentIcon}><Icons
                                            icon={activeSubTabIndex === index ? ICONS.FOLDER_OPENED : ICONS.FOLDER_CLOSED}/></div>
                                    }
                                    <H5 className={activeSubTabIndex === index ? styles.selectedChildHeaderTextStyle : styles.childHeaderTextStyle}>
                                        {detail.name}
                                    </H5>
                                    {detail.itemCounterEnabled && (detail.count || detail.count === 0) ?
                                        <H5 className={activeSubTabIndex === index ? styles.selectedChildHeaderTextStyle : styles.childHeaderTextStyle}>
                                            {'(' + detail.count + ')'}
                                        </H5> : detail.itemCounterEnabled ?
                                            <H5 className={activeSubTabIndex === index ? styles.selectedChildHeaderTextStyle : styles.childHeaderTextStyle}>{'(...)'}</H5> : null}
                                </div>
                            })}
                        </div>
                    </Collapse>
                    </div>
                </div>
            })}
        </div>
    );
}

