import React, {useState} from 'react'
import {Button, H5} from "@blueprintjs/core";
import styles from "./loginPopover.module.scss";
import {useTranslation} from 'react-i18next';
import {INPUT_TYPE} from "../../../utils/common";
import {ConfirmationDialog, InputBox} from "../../../components";
import {login} from "../../../api";
import {useSelector} from "react-redux";

const LoginPopover = (props) => {
    const {t} = useTranslation();
    const {onRetry} = useSelector((state) => state.dashboard);

    const [showPassword, setShowPassword] = useState(false);
    const [passwordState, setPasswordState] = useState("");
    const [passwordErrorState, setPasswordErrorState] = useState(t('validation_required_password'));
    const [invalidDataErrorState, setInvalidDataErrorState] = useState(t('message_session_expiry'));

    const userName = localStorage.getItem('username');

    const handleLockClick = () => {
        setShowPassword(!showPassword);
    };

    const onPasswordChange = (event) => {
        let value = event.target.value;
        value = value.replace(/\s/g, "");
        setPasswordState(value);
        setPasswordErrorState("");
        setInvalidDataErrorState("")
    };

    const onSignInClick = async () => {
        let errorFlag = false;

        if (passwordState) {
            setPasswordErrorState("")
        } else {
            errorFlag = true;
            setPasswordErrorState(t('login.password.required'))
        }
        if (!errorFlag) {
            try {
                const response = await login(userName, passwordState, props.title, onRetry);
                if (response.status === 200) {
                    props.onClose();
                }
            } catch (e) {
                setInvalidDataErrorState(e)
            }
        }
    };

    const onResetClick = () => {
        setPasswordState("");
        setShowPassword(false);
    };

    return <>
        { props.showPopover &&
            <ConfirmationDialog
                showDialog={true}
                headerText={t('label.login.signin')}
                width={600}
                zIndex={1000}
                onClose={props.onClose}
                body={<div className={styles.centerBox}>
                    <div className={styles.loginBox}>
                        <div className={styles.loginContent}>
                            <div className={styles.textLayout}>
                                <H5 className={styles.textLabel}>
                                    <div className={styles.requiredText}>*</div>
                                    {t('label.login.username')}</H5>
                                <div className={styles.inputContainer}>
                                    <InputBox
                                        inputType={INPUT_TYPE.TEXT}
                                        isDisabled={true}
                                        placeholder={t('label.login.username.placeholder')}
                                        value={userName}
                                        maxLength={50}
                                        width={'250px'}
                                        height={'24px'}
                                    />
                                </div>
                            </div>
                            <div className={styles.textLayout}>
                                <H5 className={styles.textLabel}>
                                    <div className={styles.requiredText}>*</div>
                                    {t('label.login.password')}</H5>
                                <div className={styles.inputContainer}>
                                    <InputBox
                                        autoFocus={false}
                                        inputType={showPassword ? INPUT_TYPE.TEXT : INPUT_TYPE.PASSWORD}
                                        isDisabled={false}
                                        onChange={onPasswordChange}
                                        placeholder={t('label.login.password.placeholder')}
                                        errorText={passwordErrorState}
                                        value={passwordState}
                                        iconName={showPassword ? "eye-open" : "eye-off"}
                                        onIconClick={handleLockClick}
                                        maxLength={50}
                                        width={'250px'}
                                        height={'24px'}
                                        onKeyPressMethod={onSignInClick}
                                    />
                                </div>
                            </div>
                            <div className={styles.errorDiv}>{invalidDataErrorState}</div>
                            <Button className={styles.button}
                                    onClick={onSignInClick}>
                                {t('label.login.signin')}
                            </Button>
                            <Button className={styles.button}
                                    onClick={onResetClick}>
                                {t('label.login.reset')}
                            </Button>
                        </div>
                    </div>
                </div>}/>
        }
    </>

};

export default LoginPopover;
