import React, {useEffect, useState} from 'react'
import {Button, Label} from "@blueprintjs/core";
import {ConfirmationDialog, HeaderActions, Icons} from '../../../components'
import {
    ReflexContainer,
    ReflexSplitter,
    ReflexElement
} from 'react-reflex'
import { ICONS} from "../../../utils/iconNames";
import styles from "./workSearch.module.scss";
import {Table} from "../../../components/tables/Table";
import {useDispatch, useSelector} from "react-redux";
import {getSearchData, sessionCheck} from "../../../api";
import {AutoSelect} from "../../../components/autoSelect";
import {SearchCriteriaTable} from "../../../components/tables/SearchCriteriaTable";
import {
    setCurrentTemplate,
    setCriteriaDefinitions,
    setResultSetData,
    setResultSetDefData,
    setError
} from "../../../slices/dashboardSlice";
import {useTranslation} from "react-i18next";
import {SORT_DIRECTION} from "../../../utils/common";
import ReassignUser from "./ReassignUser";
import {setShowRouteSuccessPopup, setShowSuccessPopup, setViewPageLoading} from "../../../slices/workSearchSlice";
import {checkIsViewReady} from "../../../api/workSearch";

const WorkSearchComponent = () => {
    const {hasValid, noContext,criteriaDefinitionsData,resultSetDefinitionsData,loading,resultSetData,currentTemplate,choiceList,defaultTemplate, totalSearches}=useSelector((state) => state.dashboard);
    const {reassignPopupLoading,routePopupLoading,reassignSuccessPopup,reassignRouteSuccessPopup,viewPageLoading}=useSelector((state) => state.workSearch);

    const [isError,setIsError]=useState(false);
    const [showResetConfirmation,setShowResetConfirmation]=useState(false);
    const [showSortValidationPopup,setShowSortValidationPopup]=useState(false);
    const [showHeaderFilters,setShowHeaderFilters]=useState(true);
    const [selectedRows,setSelectedRows]=useState(null);
    const [resetValidationsData, setResetValidationsData]=useState(false);
    const [searchClick, setSearchClick]=useState({isSearchBtnClick:false, index:0});
    const [resetClick, setResetClick]=useState(false);
    const [tableHeader, setTableHeader]=useState([]);
    const [showReassignPopup, setShowReassignPopup]=useState(false);
    const [showRoutePopup, setShowRoutePopup]=useState(false);

    const dispatch = useDispatch();
    const {t} = useTranslation();

    const headerWSIcon = <Icons icon={ICONS.MODULE_WORK_SEARCH} />;
    const headerSRIcon = <Icons icon={ICONS.GRID_SHOW} />;
    const startIcon = <Icons icon={ICONS.WORK_GREEN_FLAG} />;
    const searchIcon = <Icons icon={ICONS.DOC_SEARCH_ALL} />;
    const routeIcon = <Icons icon={ICONS.WORK_FORWARD} />;
    const viewIcon = <Icons icon={ICONS.DOC_VIEW} />;
    const takeIcon = <Icons icon={ICONS.WORK_VIEW_LOCKED} />;
    const resetIcon = <Icons icon={ICONS.DOC_REFRESH} />;

    let validations = criteriaDefinitionsData?.filter((d) => d.visibility === 3 || d.visibility === 4);
    const resultButtonActions = [];
    currentTemplate.startEnabled && resultButtonActions.push(<Button icon={startIcon} text={t('btn.searchbutton.start')} title={t('btn.searchbutton.start')} className={styles.buttonIcon}/>);
    currentTemplate.findEnabled && resultButtonActions.push(<Button icon={searchIcon} text={t('btn.searchbutton.find')} title={t('btn.searchbutton.find')} className={styles.buttonIcon} onClick={()=>handleSearchClick1()}/>);
    currentTemplate.reassignEnabled && resultButtonActions.push(<Button icon={headerWSIcon} disabled={!selectedRows?.length} text={t('btn.searchbutton.reassign')} title={t('btn.searchbutton.reassign')} className={styles.buttonIcon} onClick={()=>{setShowReassignPopup(!showReassignPopup)}}/>);
    currentTemplate.routeEnabled  && resultButtonActions.push(<Button icon={routeIcon} disabled={!selectedRows?.length} text={t('btn.route.route')} title={t('btn.route.route')} className={styles.buttonIcon}  onClick={()=>{setShowRoutePopup(!showRoutePopup)}}/>);
    currentTemplate.viewEnabled && resultButtonActions.push(<Button icon={viewIcon} disabled={selectedRows?.length > 1 || !selectedRows?.length } text={t('btn.searchbutton.view')} title={t('btn.searchbutton.view')}  className={styles.buttonIcon} onClick={()=>{handleViewClick()}}/>);
    currentTemplate.takeEnabled && resultButtonActions.push(<Button icon={takeIcon} disabled={selectedRows?.length > 1 || !selectedRows?.length} text={t('btn.searchbutton.viewlocked')} title={t('btn.searchbutton.viewlocked')} className={styles.buttonIcon} />);
    resultButtonActions.push(<Button icon={resetIcon} text={t('btn.searchbutton.reset')} title={t('btn.searchbutton.reset')} className={styles.buttonIcon}  onClick={()=>setShowResetConfirmation(!showResetConfirmation)}/>);

    const templateButtonActions = [];
    templateButtonActions.push(<Button icon={showHeaderFilters?<Icons icon={ICONS.ROUNDED_UP} />:<Icons icon={ICONS.ROUNDED_DOWN} />} style={{boxShadow: "none"}} onClick={()=>setShowHeaderFilters(!showHeaderFilters)}/>);


    const getSearchResults= async (range,selectedColumn,sortDir,isSortApplied)=>{
        let validationData = criteriaDefinitionsData?.filter((d) => d.visibility === 3||  d.visibility === 4)
        if(validationData?.length<=0) {
            if (isSortApplied) {
                if (totalSearches > 100) {
                    setShowSortValidationPopup(!showSortValidationPopup);
                } else {
                    try {
                        let selectedSort = sortDir === SORT_DIRECTION.ASC ? '+' : '-';
                        await getSearchData('process',`${range.startPage === 1 ? 0 : range.startPage}-${range.endPage}`, currentTemplate, selectedColumn, selectedSort);
                    } catch (error) {
                        setIsError(true);
                        console.log('Error', error)
                    }
                }
            } else {
                await getSearchData('process',`${range.startPage ===  1 ? 0 : range.startPage}-${range.endPage}`, currentTemplate);
            }
        }
    }

    useEffect(()=>{
        let  d = resultSetDefinitionsData.map(row => {
            return {name:row.fieldDefinition.name, caption:row.fieldDefinition.caption, colWidth:row.columnWidth}
        })
        setTableHeader(d);
    },[resultSetDefinitionsData]);

    const handleSourceCellDataChange=(value, fieldName)=>{
        let reqObj = JSON.parse(JSON.stringify(currentTemplate));
        let index= reqObj.retrievalExpressions.detailSet.findIndex(data => data.target === fieldName);
        reqObj.retrievalExpressions.detailSet[index].source=value;
        dispatch(setCurrentTemplate(reqObj));
        dispatch(setCriteriaDefinitions(reqObj));
    }

    const handleOperatorCellDataChange=(value, fieldName)=>{
        let reqObj = JSON.parse(JSON.stringify(currentTemplate));
        let index= reqObj.retrievalExpressions.detailSet.findIndex(data => data.target === fieldName);
        reqObj.retrievalExpressions.detailSet[index].operator=value.id;
        reqObj.retrievalExpressions.detailSet[index].source='';
        dispatch(setCurrentTemplate(reqObj));
        dispatch(setCriteriaDefinitions(reqObj));
    }

    const handleProcessFieldChange = (value, fieldName,e)=>{
        let reqObj = JSON.parse(JSON.stringify(currentTemplate));
        reqObj.workflowId=value;
        dispatch(setCurrentTemplate(reqObj));
        dispatch(setCriteriaDefinitions(reqObj));
        if(e.key==='Enter'){
            getSearchData('process','0-24',reqObj)
        }
    }

    const handleSortByFieldChange = (value, fieldName,e)=>{
        let reqObj = JSON.parse(JSON.stringify(currentTemplate));
        reqObj.indexName=value;
        dispatch(setCurrentTemplate(reqObj));
        dispatch(setCriteriaDefinitions(reqObj));
        if(e.key==='Enter'){
            getSearchData('process','0-24',reqObj)
        }
    }

    const handleSearchClick=(flag)=>{
        if(hasValid || flag){
            getSearchData('process','0-24', currentTemplate )
        }else{
            dispatch(setResultSetData(null));
        }
    }
    const handleSearchClick1=()=>{
      setSearchClick({isSearchBtnClick:true,index:Math.floor(Math.random() * 10)});
      dispatch(setResultSetData(null));
      setSelectedRows([]);
    }

    const handleResetClick=()=>{
        dispatch(setCriteriaDefinitions(defaultTemplate));
        dispatch(setCurrentTemplate(defaultTemplate));
        dispatch(setResultSetData(null));
        setShowResetConfirmation(!showResetConfirmation);
        setResetValidationsData(true);
        setResetClick(!resetClick);
        setSelectedRows([]);
    }

    const actionsOnTableRowClick=async (d,i,isDoubleClicked)=>{
        if(!Array.isArray(d)){
            setSelectedRows([d])
        }else{
            setSelectedRows(d)
        }
        if(isDoubleClicked){
            let row=!Array.isArray(d)?[d]:d;
            let session= await sessionCheck();
            if(session.message==='success') {
                let resp = await checkIsViewReady(row[0]?.id);
                if(resp.message==='success') {
                    window.open(window.location + `/work-viewer/${row[0]?.id}`, "", "width=1080,height=850");
                }else{
                    //Need this message from backend
                    dispatch(setError({title: 'Exception', message: 'A server error occurred.If this problem persists, please contact your system administrator.'}))
                }
            }
        }
    }

    const handleInfoDialog=()=>{
        dispatch(setShowSuccessPopup(false))
        dispatch(setShowRouteSuccessPopup(false))
    }

    const handleViewClick=async ()=>{
        let d= await sessionCheck();
        if(d.message==='success') {
            let resp = await checkIsViewReady(selectedRows[0]?.id);
            if(resp.message==='success') {
                window.open(window.location + `/work-viewer/${selectedRows[0]?.id}`, "", "width=1080,height=850");
            }else{
                //Need this message from backend
                dispatch(setError({title: 'Exception', message: 'A server error occurred.If this problem persists, please contact your system administrator.'}))
            }
        }
    }

    const SubHeaderComponent=(props)=>{
        let processData =currentTemplate.templateProcesses?.map(t1 => ({...t1, ...choiceList[0].selections?.find(t2 =>t2.workflowId === t1.workflowId)}))
        processData.length > 1  && processData?.splice(0, 0, {
            description: "",
            name: "",
            workflowId: "",
            workflowName: "",
        });
        return props.show ? <div className={styles.header}>
            <div className={styles.headerContent}>
                <h4  style={{paddingLeft:'10px'}}>{t('label.searchoption.in')}</h4>
                <div style={{paddingLeft:'20px', width:'40%'}} title={t('filter_pane')}> <AutoSelect
                    loadData={null}
                    loading={false}
                    unValidText={t('INVALID_SELECT_VALUE_MSG',{fieldName:t('label.searchoption.in')})}
                    editable={false}
                    dataList={[]}
                    isDisabled={true}
                    selectedValue={{value: currentTemplate.searchInName}}
                /></div>
                <h4 style={{paddingLeft:'100px'}} >{t('label.searchoption.process')}</h4>
                <div style={{paddingLeft:'20px',width:'40%'}} title={t('filter_pane')}> <AutoSelect
                    loadData={null}
                    loading={false}
                    unValidText={t('INVALID_SELECT_VALUE_MSG',{fieldName:t('label.searchoption.process')})}
                    editable={false}
                    dataList={processData ? processData : []}
                    selectedValue={{value: currentTemplate.workflowId === '0' ?processData[0].description:
                            processData?.find(t1 => t1.id === currentTemplate.workflowId)?.description}}
                    popoverWidth={165}
                    isDisabled={processData?.length<=1}
                    onItemSelected={(d,e)=>{handleProcessFieldChange(d.workflowId,'workflowId',e)}}
                /></div>
                <h4 style={{paddingLeft:'100px'}} className={styles.requiredText}>{'*'}</h4>
                <h4 style={{width:'140px'}}>{t('sysmgr.worksearch.config.general.sortBy')}</h4>
                <div style={{paddingLeft:'20px',width:'40%'}} title={t('filter_pane')}><AutoSelect
                    loadData={null}
                    loading={false}
                    unValidText={t('INVALID_SELECT_VALUE_MSG',{fieldName:t('sysmgr.worksearch.config.general.sortBy')})}
                    editable={false}
                    dataList={currentTemplate?.sortByTemplates?currentTemplate.sortByTemplates:[]}
                    selectedValue={{value: currentTemplate.indexName}}
                    onItemSelected={(d,e)=>{handleSortByFieldChange(d.sortByName,'indexName',e)}}
                    popoverWidth={165}
                    isDisabled={currentTemplate?.sortByTemplates?.length<=1}
                />
                </div>
            </div>
        </div>:<div/>
    }
    return criteriaDefinitionsData.length ?<ReflexContainer orientation="horizontal">
        <ReflexElement flex={0.2} style={showHeaderFilters ? {maxHeight:'60px',overflow:'visible'} : {maxHeight:'30px',overflow:'visible'}} >
            <div className={styles.header}>
                <div className={styles.headerContainerStyle}>
                    <h4 className={styles.headerIcon}>{headerWSIcon}</h4>
                    <h4 className={styles.headerContent}>{currentTemplate.name?t('toolseo.toolname.tool7')+' : '+currentTemplate.name:''}</h4>
                </div>
                <HeaderActions minimal={true} actions={templateButtonActions}/>
            </div>
            <SubHeaderComponent show={showHeaderFilters}/>
        </ReflexElement>
        <ReflexElement flex={0.6} style={{overflow:'auto'}}>
           <SearchCriteriaTable
                tableId={'searchFilters'}
                headerIcon={headerWSIcon}
                headerTitle={currentTemplate.name?t('toolseo.toolname.tool7')+' : '+currentTemplate.name:''}
                actions={<HeaderActions minimal={true} actions={templateButtonActions}/>}
                tableData={criteriaDefinitionsData}
                subHeaderComponent={<SubHeaderComponent show={showHeaderFilters}/>}
                handleSourceCellDataChange={handleSourceCellDataChange}
                handleOperatorCellDataChange={handleOperatorCellDataChange}
                resetValidationsData={resetValidationsData}
                setResetValidationsData={setResetValidationsData}
                getSearchResultData={handleSearchClick}
                searchClick={searchClick}
                resetClick={resetClick}
        />
            {showResetConfirmation && <ConfirmationDialog
                isCloseButtonShown={true}
                headerText={'Confirmation'}
                icon={<Icons icon={ICONS.QUESTION_MARK_ICON}/>}
                width={420}
                divideByX={3}
                divideByY={5.5}
                showDialog={showResetConfirmation}
                onClose={()=>setShowResetConfirmation(!showResetConfirmation)}
                noButtonText={t('ipd.cag.no')}
                onNoBtnClick={()=>setShowResetConfirmation(!showResetConfirmation)}
                yesButtonText={t('ipd.cag.yes')}
                onYesBtnClick={()=>handleResetClick()}
                body={<div style={{height:'130px'}}>
                    <div>{t('reset.confirmation.message')}</div>
                    <div style={{display:'flex'}}>
                        {t('reset.confirmation.click')} <div className={styles.resetTextStyle}>{t('ipd.cag.yes')}</div>{t('reset.confirmation.continue')}
                        <div className={styles.resetTextStyle}>{t('ipd.cag.no')}</div> {t('reset.confirmation.cancel')}
                    </div>
                </div>}
                isDismissOnEnterKey={true}
            />}
            { showSortValidationPopup && <ConfirmationDialog
                headerText={'Information'}
                width={600}
                divideByX={3.5}
                divideByY={5}
                icon={<Icons icon={ICONS.INFO}/>}
                showDialog={showSortValidationPopup}
                onClose={()=>setShowSortValidationPopup(false)}
                yesButtonText={t('btn.translationOk.ok')}
                onYesBtnClick={()=>setShowSortValidationPopup(false)}
                body={<div>{t('MAX_SORT_EXCEEDED',{recordLength:100})}</div>}
                isDismissOnEnterKey={true}
                isCloseButtonShown={true}
            />}
            { viewPageLoading && <ConfirmationDialog
                width={300}
                divideByX={2.8}
                divideByY={4.5}
                showDialog={viewPageLoading}
                icon={<div className={styles.loadingGifStyle}/>}
                headerText={t('progress_popup_header')}
                onClose={()=>dispatch(setViewPageLoading(false))}
                body={<Label>{t('OPEN_WORK_ITEM')}</Label>}
                onNoBtnClick={()=>dispatch(setViewPageLoading(false))}
                transitionDuration={300}
                isCloseButtonShown={false}/>}
        </ReflexElement>
        <ReflexSplitter className="horizontal-splitter" >
            <div className="horizontal-splitter-thumb"/>
        </ReflexSplitter>
        <ReflexElement maxSize={window.screen.height-350} flex={0.4}>
            <Table
                loading={loading}
                tableId={'searchRes'}
                headerIcon={headerSRIcon}
                headerTitle={t('search.result')}
                actions={<HeaderActions minimal={true} actions={resultButtonActions} />}
                searchResultCol={tableHeader}
                tableData={resultSetData}
                tableName={t('search.result')}
                showPageFooter={true}
                isSortable={true}
                getSearchResults={getSearchResults}
                isError={isError}
                actionsOnTableRowClick={actionsOnTableRowClick}
                setTableColumnsWidth={setTableHeader}
                maxSortRowLength={100}
                isValidationPanelOpen={validations.length > 0}
                resetClick={resetClick}
                searchClick={searchClick}
            />
        </ReflexElement>
        <>
            {(reassignPopupLoading || routePopupLoading) &&
            <ConfirmationDialog
                showDialog={true}
                divideByX={3}
                divideByY={3.5}
                icon={<div className={styles.loadingGifStyle}/>}
                headerText={t('progress_popup_header')}
                body={<Label>{reassignPopupLoading?`${t('REASSIGN_BUSY_MSG')}`:`${t('ROUTE_BUSY_MSG')}`}</Label>}
                transitionDuration={300}
                isCloseButtonShown={false}/>
            }
            {(reassignSuccessPopup || reassignRouteSuccessPopup)&&
                <ConfirmationDialog
                    showDialog={true}
                    width={606}
                    height={125}
                    divideByX={4}
                    divideByY={3.5}
                    icon={<Icons icon={ICONS.INFO}/>}
                    headerText={'Information'}
                    onClose={()=>handleInfoDialog()}
                    body={<Label>{reassignRouteSuccessPopup? `${t('ROUTE_SENT_MSG')}`:`${t('REASSIGN_SENT_MSG')}`}</Label>}
                    noButtonText={t('btn.common.ok')}
                    onNoBtnClick={()=>handleInfoDialog()}
                    transitionDuration={300}
                    isCloseButtonShown={true}/>
            }
            {(showReassignPopup || showRoutePopup) &&
                <ReassignUser
                    showReassignPopup={showReassignPopup}
                    showRoutePopup={showRoutePopup}
                    setShowReassignPopup={setShowReassignPopup}
                    setShowRoutePopup={setShowRoutePopup}
                    selectedRows={selectedRows}
                    isRoute={showRoutePopup}
                />
            }
        </>
    </ReflexContainer> : noContext  ? <h4 style={{paddingLeft:'10px'}}>{t('ipd.nocontext.msg')}</h4>: null
};

export default WorkSearchComponent;