import {useTranslation} from 'react-i18next';
import {ConfirmationDialog, Icons} from "../../../components";
import {useDispatch,useSelector} from "react-redux";
import {setEnableSignOutPopup} from "../../../slices/dashboardSlice";
import styles from "./signOut.module.scss";
import {signOut} from "../../../api";
import {ICONS} from "../../../utils/iconNames";
import React from "react";

const SignOut = () => {
    const {t} = useTranslation();
    const dispatch = useDispatch();
    const {enableSignOutPopup} = useSelector((state) => state.dashboard);

    const onClose = () => {
        dispatch(setEnableSignOutPopup({flag: false, title: ''}))
    };
    const handleDialog = () => {
        dispatch(setEnableSignOutPopup({flag: false, title: ''}))
    };
    const onSignOutClick = async () => {
        await signOut(enableSignOutPopup.title)
    };

    return <>
        { enableSignOutPopup.flag &&
            <ConfirmationDialog
                showDialog={true}
                icon={<Icons icon={ICONS.QUESTION_MARK_ICON}/>}
                headerText={'Confirmation'}
                width={400}
                onYesBtnClick={onSignOutClick}
                yesButtonText={t('ipd.cag.yes')}
                noButtonText={t('ipd.cag.no')}
                onNoBtnClick={handleDialog}
                isCloseButtonShown={true}
                onClose={onClose}
                body={<div className={styles.bodyStyle}>
                    <h4 className={styles.textStyle}>{t('message_popover_signout')}</h4>
                    <h4 className={styles.textStyle}>{t('label_click')}<b>{t('ipd.cag.yes')}</b>{t('label_to_continue_or')}<b>{t('ipd.cag.no')}</b> {t('label_to_cancel')}
                    </h4>
                </div>}
            />
        }
        </>
};

export default SignOut;
