import {ConfirmationDialog, Icons} from "../../../components";
import styles from "./../dashboard/dashboard.module.scss";
import React, {useEffect, useState} from "react";
import {useHistory, useLocation} from "react-router";
import {useDispatch, useSelector} from "react-redux";
import {setEnvironmentId, setOpenEnvDialog, clearContextData,
    setNoContextFound
} from "../../../slices/dashboardSlice";
import {ICONS} from "../../../utils/iconNames";
import {AutoSelect} from "../../../components/autoSelect";
import {useTranslation} from "react-i18next";
import {getEnvironmentList, navigateToDashboard} from "../../../api";
import _ from "lodash";
import {Label} from "@blueprintjs/core";

export const EnvironmentDialog = ({title, openEnvDialog}) => {
    const {environmentId, loading,environmentList}  = useSelector((state) => state.dashboard);
    const dispatch = useDispatch();
    const history = useHistory();
    const [environmentError, setEnvironmentError] = useState('');
    const {t} = useTranslation();
    const storedEnvId = localStorage.getItem("envId");


    useEffect(  () => {
        const fetchData = async () => {
           await getEnvironmentList();
        };
        fetchData()
    }, []);

    const handleEnvDialog = () => {
        dispatch(setOpenEnvDialog(false))
    };

    const onEnvironmentSelected = (data) => {
        dispatch(setEnvironmentId(data.id));
        setEnvironmentError('')
    };

    const handleEnvOkClick = async () => {
        if (!environmentId) {
            setEnvironmentError(t('ipd.framework.validation.message.required'))
        } else {
            setEnvironmentError('');
            const result = await navigateToDashboard(title, environmentId);
            if(result.status === 200) {
                dispatch(setOpenEnvDialog(false));
                localStorage.setItem('envId',environmentId);
                dispatch(setEnvironmentId(environmentId));
                dispatch(clearContextData([]));
                dispatch(setNoContextFound(false));
                history.replace('/')
            }
        }
    };
    let selectedObject = null;
    if(environmentId){
       const objValue =  _.filter(environmentList, (item)=>{
            return item.id === environmentId
        });
       if(objValue.length > 0){
           selectedObject= objValue[0]
       }
    }
    if(environmentError){
        selectedObject = null
    }

    return (
        <>
            {
                environmentList?.length > 0 && !loading ?
                <ConfirmationDialog
                    width={485}
                    showDialog={openEnvDialog}
                    headerText={`Ultera: ${t('ttl.envSelection.Title')}`}
                    onClose={handleEnvDialog}
                    body={
                        <div>
                            <div className={styles.subHeader}>
                                <div className={styles.headerLabelBox}>
                                    <Icons icon={ICONS.HOME}/>
                                    <div className={styles.headerText}>{t('ttl.envSelection.Title')}</div>
                                </div>
                                <div className={styles.requiredText}>* {t('label.common.required')}</div>
                            </div>
                            <div style={{height: 300, paddingTop: 15}}>
                                <div className={styles.autoSelectBox}>
                                    <div className={styles.requiredText}>*</div>
                                    <div className={styles.boldText}>{t('label.envNn.envName')}</div>
                                    <div className={styles.autoSelectDiv}>

                                        <AutoSelect
                                            loadData={null}
                                            loading={false}
                                            popoverWidth={350}
                                            editable={false}
                                            errorText={environmentError}
                                            dataList={environmentList}
                                            onItemSelected={onEnvironmentSelected}
                                            selectedValue={selectedObject}
                                            tooltipPosition={'bottom-right'} setErrorText={setEnvironmentError}
                                            unValidText={t('validation_text_invalid_required')}/>
                                    </div>
                                </div>

                            </div>
                        </div>
                    }
                    transitionDuration={300}
                    isCloseButtonShown={storedEnvId ? true : false}
                    noButtonText={storedEnvId && t('btn.common.cancel')}
                    onNoBtnClick={storedEnvId && handleEnvDialog}
                    noButtonTooltip={t('ttl.envSelection.Title')}
                    yesButtonText={t('btn.common.ok')}
                    onYesBtnClick={handleEnvOkClick}
                    yesButtonTooltip={t('ttl.envSelection.Title')}
                    environmentError={environmentError}/>
                    :
                    openEnvDialog && !loading &&
                    <ConfirmationDialog
                        showDialog={openEnvDialog}
                        width={350}
                        headerText={"Information"}
                        icon={<Icons icon={ICONS.ENV_LOG}/>}
                        onClose={handleEnvDialog}
                        body={
                            <div>
                                {t('ultera_no_env_config_msg')}
                            </div>
                        }
                        isCloseButtonShown={true}
                        yesButtonText={t('btn.common.ok')}
                        onYesBtnClick={handleEnvDialog}/>
            }
            {
                loading &&
                <ConfirmationDialog
                    showDialog={true}
                    icon={<div className={styles.loadingGifStyle}/>}
                    headerText={t('progress_popup_header')}
                    body={<Label>{t('message_pleaseWait')}</Label>}
                    transitionDuration={300}
                    isCloseButtonShown={false}/>
            }
        </>
    );
};
