import React, {useEffect, useState} from "react";
import {useParams} from "react-router";
import {getViewTemplateData} from "../../../../api/workSearch";
import {Helmet} from "react-helmet";
import styles from "./viewWorkItem.module.scss";
import {Header, HeaderActions, Icons} from "../../../../components";
import {useTranslation} from "react-i18next";
import {Button, Collapse, Divider, H5, Intent, Menu, MenuItem, TextArea} from "@blueprintjs/core";
import {ICONS} from "../../../../utils/iconNames";
import {ReflexContainer, ReflexElement, ReflexSplitter} from "react-reflex";
import {useSelector} from "react-redux";
import {ViewWorkTable} from "./ViewWorkTable";
import {ValidationAssistant} from "../../../../components/tables/ValidationAssistant";

export const ViewWorkItem = (props) => {
    let { id } = useParams();
    const {t} = useTranslation();
    const {workObject,workLeftPanelData,viewPageLoading}=useSelector((state) => state.workSearch);
    let activeSubTabIndex=null;
    const [showValidationAssistant,setShowValidationAssistant]=useState(false);

    useEffect(()=>{
        if(id){
            getViewTemplateData(id);
        }
    },[id]);

    const HelpMenu = (
        <Menu className={styles.menu}>
            <MenuItem className={styles.subMenu} icon={<Icons icon={ICONS.HELP}/>} text={t('toolseo.toolname.HelpContents')}/>
        </Menu>);
    const actions = [{"name": t('toolseo.toolname.toolHelpMenu'), "action"  : HelpMenu}];

    const headerActions = [];
    headerActions.push(<Button title={t('title.create.task')} icon={<Icons icon={ICONS.WORK_FILE} />} text={t('title.create.task')}/>);
    headerActions.push(<Button  title={t('toolseo.toolname.toolLockWork')} icon={<Icons icon={ICONS.WORK_LOCK} />} text={t('toolseo.toolname.toolLockWork')} />);
    headerActions.push(<Divider className={styles.divider} />);

    let test=[
        // {icon:ICONS.PORTAL_RENAME,btnName:t('toolseo.toolname.ipd.search.rename')},
    ]
    const handleClick=(index, route)=>{
        console.log('**handleClick',route)
    }
    const handleSubTabClick=(index, route)=>{
        console.log('**handleClick',route)
    }

    return(
        <>
        <Helmet>
            <title>{workObject?.subject? `Ultera:${t('title.process.viewer')} | `+ workObject?.subject:`Ultera:${t('title.process.viewer')}`}</title>
        </Helmet>
            {viewPageLoading ? <div className={styles.pageLoading}><div className={styles.loadingGifStyle}/><h3 style={{paddingLeft:'10px'}}>{t('status.renderPage')}</h3></div>:<div className={styles.dashboard}>
                <Header actions={actions}/>
                <div className={styles.header}>
                    <HeaderActions minimal={true} actions={test} isHeader={true} commonActions={headerActions}/>
                </div>
                <ReflexContainer orientation="horizontal" style={{height: window.screen.height}}>
                    <ReflexElement size={!showValidationAssistant ?  window.screen.height : window.screen.height - 100}>
                        <ReflexContainer orientation="horizontal">
                            <ReflexElement maxSize={1} minSize={0}>
                            </ReflexElement>
                            <ReflexSplitter className="horizontal-splitter disabled"/>
                            <ReflexElement>
                                <ReflexContainer orientation="vertical">
                                    <ReflexElement flex={1} minSize={150}
                                                   onResize={(e)=>{localStorage.setItem('__default_home_left',e.domElement.clientWidth)}} >
                                            <div className={styles.leftPanel}>
                                                <div key={'tab'} className={styles.containerStyle }>
                                                        <div className={ styles.selectedTabContainerStyle}
                                                             onClick={()=> handleClick(1, '')}>
                                                            <div className={styles.headerContainerStyle}>
                                                                <div className={styles.headerIconTitleContainerStyle}>
                                                                    <div><Icons icon={ICONS.WORK_EDIT}/></div>
                                                                    <H5 className={styles.headerTextStyle}>
                                                                        {workObject?.subject}
                                                                    </H5>
                                                                </div>
                                                                <div className={styles.loadingIconContainerStyle}>
                                                                    {viewPageLoading && <div className={styles.loadingGifStyle}/>}
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className={styles.collapsableContainerStyle}>
                                                            <Collapse className={styles.collapsableActivePanel} isOpen={true}>
                                                                <div className={styles.collapsableListContainer}>
                                                                    {workLeftPanelData?.items?.map((detail, index) => {
                                                                        return <div key={index}
                                                                                    className={activeSubTabIndex === index ? styles.selectedDetailsContainerStyle : styles.detailsContainerStyle}
                                                                                    onClick={() => handleSubTabClick(index, detail)}>
                                                                            { viewPageLoading ?
                                                                                <div className={styles.loadingGifStyle}/> :
                                                                                <div className={styles.subDataParentIcon}><Icons
                                                                                    icon={activeSubTabIndex === index ? ICONS.FOLDER_OPENED : ICONS.FOLDER_CLOSED}/></div>
                                                                            }
                                                                            <H5 className={activeSubTabIndex === index ? styles.selectedChildHeaderTextStyle : styles.childHeaderTextStyle}>
                                                                                {detail.name}
                                                                            </H5>
                                                                            {detail.countable && (detail.count || detail.count === 0) ?
                                                                                <H5 className={activeSubTabIndex === index ? styles.selectedChildHeaderTextStyle : styles.childHeaderTextStyle}>
                                                                                    {'(' + detail.count + ')'}
                                                                                </H5> : detail.itemCounterEnabled ?
                                                                                    <H5 className={activeSubTabIndex === index ? styles.selectedChildHeaderTextStyle : styles.childHeaderTextStyle}>{'(...)'}</H5> : null}
                                                                        </div>
                                                                    })}
                                                                </div>
                                                            </Collapse>
                                                        </div>
                                                    </div>
                                            </div>
                                    </ReflexElement>
                                    <ReflexSplitter className="vertical-splitter">
                                        <div className="vertical-splitter-thumb"/>
                                    </ReflexSplitter>
                                    <ReflexElement flex={3} >
                                        <ReflexContainer rientation="horizontal">
                                            <ReflexElement flex={0.2} style={{ minHeight:'51px',}}>
                                                <div className={styles.processHeader}>
                                                    <Icons icon={ICONS.WORK_FILE}/><h4 style={{paddingLeft:'5px'}}>{workObject?.stepName}</h4>
                                                </div>
                                                <div>
                                                    <div style={{ paddingLeft:'10px'}}>
                                                        <div><h4>{t('ipd.stepview.comments.label')}</h4></div>
                                                        <div style={{width:'95%'}} tabIndex={0}>
                                                            <TextArea
                                                                style={{resize: 'none', fontFamily: 'Verdana, Arial, Helvetica, sans-serif'}}
                                                                growVertically={true}
                                                                large={true}
                                                                autoFocus={true}
                                                                fill={true}
                                                                intent={Intent.PRIMARY}
                                                                value={workObject?.comment}
                                                                disabled={true}
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                            </ReflexElement>
                                            <ReflexSplitter className="horizontal-splitter" >
                                                <div className="horizontal-splitter-thumb"/>
                                            </ReflexSplitter>
                                            <ReflexElement style={{ minHeight:'51px', backgroundColor:'#ffffff'}} >
                                                <ViewWorkTable workObject={workObject}/>
                                            </ReflexElement>
                                        </ReflexContainer>
                                    </ReflexElement>
                                </ReflexContainer>
                            </ReflexElement>
                        </ReflexContainer>
                    </ReflexElement>
                    <ReflexSplitter className="horizontal-splitter" onStartResize={()=>setShowValidationAssistant(true)} >
                        <div className="horizontal-splitter-thumb" onClick={()=>setShowValidationAssistant(true)}/>
                    </ReflexSplitter>
                    <ReflexElement size={!showValidationAssistant && 1} style={{overflow:"unset", backgroundColor:'#ffffff'}}>
                        {showValidationAssistant && <ValidationAssistant
                            searchCriteriaValidationData={[]}
                            setOpenValidationAssistant={setShowValidationAssistant}
                            actionsOnTableRowClick={()=>{}}
                            onContinueClick={()=>{}}
                            tableData={[]}
                            onColumnSort={()=>{}}
                            isFullWidth={true}
                            isContinueDisabled={true}
                    />}</ReflexElement>
                </ReflexContainer>
            </div>}
    </>)
}