import React, {useState, useEffect} from 'react'
import {Button, H1, H5} from "@blueprintjs/core";
import styles from "./login.module.scss";
import {useTranslation} from 'react-i18next';
import {INPUT_TYPE} from "../../../utils/common";
import {InputBox} from "../../../components/inputBox";
import {getEnvironmentList, login, navigateToDashboard} from "../../../api";
import { useHistory } from "react-router-dom";
import {Helmet} from "react-helmet";
import {useDispatch, useSelector} from "react-redux";
import {setEnvironmentId, setOpenEnvDialog} from "../../../slices/dashboardSlice";
import packageJson from '../../../../package.json';
import { buildNumber } from './buildNumber';

const LoginScreen = ({title}) => {
    const {environmentId}  = useSelector((state) => state.dashboard);
    const [showPassword, setShowPassword] = useState(false);
    const [passwordState, setPasswordState] = useState("");
    const [passwordErrorState, setPasswordErrorState] = useState("");
    const [invalidDataErrorState, setInvalidDataErrorState] = useState("");
    const [usernameState, setUsernameState] = useState('');
    const [usernameErrorState, setUsernameErrorState] = useState('');
    const {t} = useTranslation();
    const date = new Date();
    const history = useHistory();
    const dispatch = useDispatch();
    const username = localStorage.getItem('username');

    useEffect(async () => {
        const envIdFromLocal =  localStorage.getItem('envId');
        if(envIdFromLocal){
            dispatch(setEnvironmentId(envIdFromLocal))
        }
        if(username) {
            history.push('/')
        }
    }, []);

    const handleLockClick = () => {
        setShowPassword(!showPassword);
    };

    const onUsernameChange = (event) => {
        setUsernameState(event.target.value);
        setUsernameErrorState("")
    };
    const onPasswordChange = (event) => {
        let value = event.target.value;
        value = value.replace(/\s/g, "");
        setPasswordState(value);
        setPasswordErrorState("");
        setInvalidDataErrorState("")
    };

    const onSignInClick = async () => {
        let errorFlag = false;
        if (usernameState) {
            setUsernameErrorState("");
        } else {
            errorFlag = true;
            setUsernameErrorState(t('login.userName.required'))
        }
        if (passwordState) {
            setPasswordErrorState("")
        } else {
            errorFlag = true;
            setPasswordErrorState(t('login.password.required'))
        }
        if (!errorFlag) {
            try {
                const response  = await login(usernameState, passwordState, title);
                if(response.status === 200){
                    if(title === "Ultera") {
                        const environments = await getEnvironmentList();
                        const envList = environments?.data;
                        if (envList?.length === 1) {
                            const result = await navigateToDashboard(title, envList[0].id);
                            localStorage.setItem('envId', envList[0].id);
                            dispatch(setEnvironmentId(envList[0].id));
                            if (result.status === 200) {
                                history.push('/')
                            }
                        } else if (envList?.length > 1) {
                            if (environmentId === null) {
                                history.push('/');
                                dispatch(setOpenEnvDialog(true))
                            } else {
                                history.push('/');
                            }
                        } else {
                            history.push('/');
                            dispatch(setOpenEnvDialog(true))
                        }
                    }else{
                        history.push('/');
                    }
                }
            } catch (e) {
                setInvalidDataErrorState('* '+t('com.ipd.signin.invalid'))
            }
        }
    };

    const onResetClick = () => {
        setUsernameState('');
        setPasswordState("");
        setShowPassword(false);
    };

    const formatVersion = (version) => {
        const versionPart = version.split('.');
        return `${versionPart[0]}${versionPart[1] !== '0' ? '.' + versionPart[1] : ''}`
    }

    return <div className={styles.centerBox}>
        <Helmet>
            <title>{title === 'Ultera' ? t('ttl.app.Name')+':'+t('ultera.login.title.window') : t('ttl.app.Name')+':'+t('admin.login.title.window')}</title>
        </Helmet>
        <div className={styles.loginBox}>
            <div className={styles.loginHeader}>
                <div style={{display: 'flex', flexDirection: 'column'}}>
                    <H1>{t(title === "Ultera" ? 'ultera.login.productname' : 'admin.login.productname')}</H1>
                    <H5 className={styles.subHeader}>{`${t('label.login.version')} ${formatVersion(packageJson.version)}`}</H5>
                </div>
                <div className={styles.companyLogo}/>
            </div>
            <div className={styles.loginContent}>
                <div className={styles.textLayout}>
                    <H5 className={styles.textLabel}>
                        <div className={styles.requiredText}>*</div>
                        {t('label.login.username')}</H5>
                    <div style={{flexGrow: 3}}>
                        <InputBox
                            inputType={INPUT_TYPE.TEXT}
                            isDisabled={false}
                            onChange={onUsernameChange}
                            placeholder={t('label.login.username.placeholder')}
                            errorText={usernameErrorState}
                            value={usernameState}
                            maxLength={50}
                            width={'250px'}
                            height={'24px'}
                        />
                    </div>
                </div>
                <div className={styles.textLayout}>
                    <H5 className={styles.textLabel}>
                        <div className={styles.requiredText}>*</div>
                        {t('label.password')}</H5>
                    <div style={{flexGrow: 3}}>
                        <InputBox
                            inputType={showPassword ? INPUT_TYPE.TEXT : "password"}
                            isDisabled={false}
                            onChange={onPasswordChange}
                            placeholder={t('label.login.password.placeholder')}
                            errorText={passwordErrorState}
                            value={passwordState}
                            iconName={showPassword ? "eye-open" : "eye-off"}
                            onIconClick={handleLockClick}
                            maxLength={50}
                            width={'250px'}
                            height={'24px'}
                            onKeyPressMethod={onSignInClick}
                        />
                    </div>
                </div>
                {invalidDataErrorState ?<div className={styles.errorDiv}>{invalidDataErrorState}</div>:null}
                <Button className={styles.button}
                        onClick={onSignInClick}>
                    {t('label.login.signin')}
                </Button>
                <Button className={styles.button}
                        onClick={onResetClick}>
                    {t('label.login.reset')}
                </Button>
                <div className={styles.bottomContent}>
                    <h5 className={styles.bottomText}>{` 2008-${date.getFullYear()} ${t('label.login.copyright')}`}</h5>
                    <h5 className={styles.bottomText}>{`${t('label.login.build')} ${buildNumber.buildNumber}`}</h5>
                </div>
            </div>
            <h5 className={styles.footerText}>{t('label.login.lgpllibraryuse ')}<a
                href={"https://www.gnu.org/licenses/lgpl-2.1.html"} className={styles.link}>
                https://www.gnu.org/licenses/lgpl-2.1.html</a></h5>
        </div>
    </div>
};

export default LoginScreen;
