import {ConfirmationDialog, Icons} from "../../../components";
import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import styles from "./workSearch.module.scss";
import {ICONS} from "../../../utils/iconNames";
import {AutoSelect} from "../../../components/autoSelect";
import {getUserListForReassign} from "../../../api";
import {useDispatch, useSelector} from "react-redux";
import {getReassignQueueList, getReassignRouteUser, getRouteReasons, reassignUser} from "../../../api/workSearch";
import {setUserList} from "../../../slices/dashboardSlice";

const ReassignUser=(props)=>{
    const {t} = useTranslation();
    const dispatch = useDispatch();
    const {showReassignPopup, setShowReassignPopup, selectedRows, setShowRoutePopup, showRoutePopup, isRoute}=props;
    const {userList, currentTemplate}=useSelector((state) => state.dashboard);
    const {routeUserList,routeReasonList,queueList}=useSelector((state) => state.workSearch);
    const [selectedUser,setSelectedUser]=useState('')
    const [selectedQueue,setSelectedQueue]=useState('')
    const [selectedReason,setSelectedReason]=useState('')
    const [routeLoading,setRouteLoading]=useState('')
    const [queueLoading,setQueueLoading]=useState('')
    const [reasonLoading,setReasonLoading]=useState('')
    const [reassignQueueError,setReassignQueueError]=useState('')
    const [reassignUserError,setReassignUserError]=useState('')
    const [reassignReasonError,setReassignReasonError]=useState('')
    const [query,setQuery]=useState('');
    const [query1,setQuery1]=useState('');
    const [reasonQuery,setReasonQuery]=useState('');

    useEffect(()=>{
        dispatch(setUserList([]))
    },[]);

    const getRouteQueueData=async ()=>{
        setQueueLoading(true)
            await getReassignQueueList()
        setQueueLoading(false)
    }
    const getRouteUserData=async (query='')=>{
        setRouteLoading(true)
        if(selectedQueue) {
            await getReassignRouteUser(selectedQueue.name, query)
        }
        setRouteLoading(false)
    }
    const getRouteReasonData=async (query='')=>{
        setReasonLoading(true)
        if(selectedQueue) {
            await getRouteReasons(selectedQueue.name, query)
        }
        setReasonLoading(false)
    }

    const handleOkClick=()=>{
        setShowRoutePopup(false);
        setShowReassignPopup(false);
        let source=[]
        selectedRows.forEach(row=>{
            source.push({sourceId:row.id})
        })
        let requestObj = {}
        if(isRoute){
            requestObj = {
                action: 2,
                templateId: currentTemplate.id,
                batchJobs: source,
                newOwner: selectedUser.name,
                queue: selectedQueue.name,
                reason: selectedReason.name
            }
        }else{
            requestObj = {
                newOwner: selectedUser.name,
                action: 1,
                templateId: currentTemplate.id,
                batchJobs: source
            }
        }
        reassignUser(requestObj);
    }

    useEffect(()=>{
        if(query && !selectedUser) {
            let userData= isRoute ? routeUserList : userList;
            const filtered = userData.filter(data => (data.description ?? data.name ?? data.caption)?.toLowerCase()?.includes(query?.toLowerCase()));
            if (filtered.length <= 0) {
                setReassignUserError(t('INVALID_SELECT_VALUE_MSG', {fieldName: t('worksearch.reassign.name.label')}))
            } else {
                setReassignUserError('')
            }
        }else {
            setReassignUserError('')
        }
    },[routeUserList, userList, query]);

    useEffect(()=>{
        if(reasonQuery) {
            const filtered = routeReasonList.filter(data => (data.description ?? data.name ?? data.caption)?.toLowerCase()?.includes(reasonQuery?.toLowerCase()));
            if (filtered.length <= 0) {
                setReassignReasonError(t('INVALID_SELECT_VALUE_MSG', {fieldName: t('worksearch.reassign.name.label')}))
            } else {
                setReassignReasonError('')
            }
        }else {
            setReassignReasonError('')
        }
    },[routeReasonList]);

    return (showReassignPopup||showRoutePopup) && <ConfirmationDialog
        width={560}
        divideByX={3.5}
        divideByY={8}
        showDialog={showReassignPopup||showRoutePopup}
        headerText={ isRoute? `Ultera: ${t('com.ipd.process.massroute.header.title')}` :`Ultera: ${t('com.ipd.process.reassign.dialog.title')}`}
        onClose={()=>{
            showReassignPopup ? setShowReassignPopup(!showReassignPopup): setShowRoutePopup(!showRoutePopup)
        }}
        transitionDuration={300}
        isCloseButtonShown={true}
        noButtonText={t('btn.common.cancel')}
        onNoBtnClick={()=>{
            showReassignPopup ? setShowReassignPopup(!showReassignPopup): setShowRoutePopup(!showRoutePopup)
        }}
        yesButtonText={t('btn.common.ok')}
        onYesBtnClick={()=>{handleOkClick()}}
        environmentError={isRoute ? !selectedReason || !selectedUser : !selectedUser}
        body={<div>
                <div className={styles.subHeader} title={isRoute ? t('com.ipd.process.massroute.header.title') : t('com.ipd.process.reassign.dialog.title')}>
                    <div className={styles.headerLabelBox}>
                        <Icons icon={isRoute?ICONS.WORK_FORWARD:ICONS.MODULE_PREFERENCES}/>
                        <div className={styles.headerText}>{isRoute ? t('com.ipd.process.massroute.header.title') : t('com.ipd.process.reassign.dialog.title')}</div>
                    </div>
                    <div className={styles.requiredText}>* {t('label.common.required')}</div>
                </div>
            {isRoute ?
               <>
                   <div className={styles.routeContentDiv}>
                       <div className={styles.autoSelectBox}>
                           <div className={styles.requiredText}>*</div>
                           <div className={styles.boldText}>{t('com.ipd.process.massroute.queue.caption')}</div>
                           <div className={styles.autoSelectDiv}>
                               <AutoSelect
                                   needBlur={true}
                                   onBlurEventHandle={()=>{
                                           !reassignQueueError && !selectedQueue && setReassignQueueError(t('ipd.framework.validation.message.required'))
                                   }}
                                   errorText={reassignQueueError?reassignQueueError:''}
                                   loadData={null}
                                   loading={queueLoading}
                                   popoverWidth={250}
                                   editable={false}
                                   dataList={queueList?queueList:[]}
                                   onArrowClick={()=>getRouteQueueData()}
                                   selectedValue={selectedQueue}
                                   onItemSelected={(item)=>{
                                       setSelectedQueue(item);
                                       setReassignQueueError('')
                                   }}
                                   setErrorText={setReassignQueueError}
                                   tooltipPosition={'left'}
                                   unValidText={t('validation_text_invalid_required')}
                               />
                           </div>
                       </div>
                       <div className={styles.autoSelectBox}>
                           <div className={styles.requiredText}>*</div>
                           <div className={styles.boldText}>{t('work.search.reassign.instruction.msg2')}</div>
                           <div className={styles.autoSelectDiv}>
                               <AutoSelect
                                   needBlur={true}
                                   onBlurEventHandle={()=>{
                                       !selectedUser && !query && setReassignUserError(t('REQ_VALUE_MSG',{fieldName:t('worksearch.reassign.name.label')}))
                                   }}
                                   errorText={reassignUserError}
                                   isDisabled={!selectedQueue}
                                   loadData={null}
                                   loading={routeLoading}
                                   popoverWidth={270}
                                   editable={false}
                                   placeHolderText={t('com.ipd.process.reassign.user.placeholder')}
                                   onArrowClick={()=>getRouteUserData('')}
                                   onSelectQueryChange={(text)=>{
                                       if(text) {
                                           getRouteUserData(text);
                                           setQuery(text);
                                       }else {
                                           getRouteUserData('');
                                           setQuery('');
                                           setSelectedUser('');
                                       }
                                   }}
                                   dataList={routeUserList?routeUserList:[]}
                                   selectedValue={selectedUser}
                                   onItemSelected={(item)=>{
                                       let selectedItem =routeUserList?.find(d1 => d1.name === item.name);
                                       if(selectedItem) {
                                           setSelectedUser(selectedItem)
                                       }else {
                                           setSelectedUser('');
                                       }
                                   }}
                                   tooltipPosition={'left'}
                               />
                           </div>
                       </div>
                       <div className={styles.autoSelectBox}>
                           <div className={styles.requiredText}>*</div>
                           <div className={styles.boldText}>{t('label.reason.notRequired')}</div>
                           <div className={styles.autoSelectDiv}>
                               <AutoSelect
                                   needBlur={true}
                                   onBlurEventHandle={()=>{
                                       !selectedReason && !reasonQuery && setReassignReasonError(t('REQ_VALUE_MSG',{fieldName:t('worksearch.reassign.name.label')}))
                                   }}
                                   errorText={reassignReasonError}
                                   isDisabled={!selectedQueue}
                                   loadData={null}
                                   loading={reasonLoading}
                                   popoverWidth={270}
                                   editable={false}
                                   onArrowClick={()=>getRouteReasonData('')}
                                   onSelectQueryChange={(text)=>{
                                       if(text){
                                           getRouteReasonData(text);
                                           setReasonQuery(text);
                                       }else {
                                           getRouteReasonData('');
                                           setReasonQuery('');
                                           setSelectedReason('');
                                       }
                                   }}
                                   dataList={routeReasonList?routeReasonList:[]}
                                   selectedValue={selectedReason}
                                   onItemSelected={(item)=>{
                                       let selectedItem=routeReasonList?.find(d1 => d1.name === item.name);
                                       if(selectedItem) {
                                           setSelectedReason(selectedItem)
                                       }else {
                                           setSelectedReason('');
                                       }
                                   }}
                                   tooltipPosition={'left'}
                               />
                           </div>
                       </div>
                   </div>
               </>
                :<>
                    <div className={styles.validationPanelHeader}  style={{paddingTop:'4px'}}>
                        <h4 className={styles.validationPanelHeaderText}>{t('com.ipd.process.reassign.label.instruction')}</h4>
                    </div>
                    <div className={styles.infoContainer}>
                        <div className={styles.infoSubContainer}>
                            <div className={styles.infoBoxTextStyle}>
                                {t('work.search.reassign.instruction.msg1')}
                                <span className={styles.reassignTextStyle}>{t('work.search.reassign.instruction.msg2')}</span>
                                {t('work.search.reassign.instruction.msg3')}
                            </div>
                        </div>
                    </div>
                    <div className={styles.contentPopupDiv}>
                            <div className={styles.autoSelectBox}>
                                <div className={styles.requiredText}>*</div>
                                <div className={styles.boldText}>{t('work.search.reassign.instruction.msg2')}</div>
                                <div className={styles.autoSelectDiv} title={t('work.search.reassign.instruction.msg2')}>
                                    <AutoSelect
                                        loadData={null}
                                        loading={false}
                                        popoverWidth={270}
                                        editable={false}
                                        errorText={reassignUserError}
                                        needBlur={true}
                                        onBlurEventHandle={()=>{
                                            !selectedUser && !query && setReassignUserError(t('REQ_VALUE_MSG',{fieldName:t('worksearch.reassign.name.label')}))
                                        }}
                                        placeHolderText={t('com.ipd.process.reassign.user.placeholder')}
                                        onArrowClick={()=>getUserListForReassign('')}
                                        dataList={userList?userList:[]}
                                        selectedValue={selectedUser}
                                        onItemSelected={(item)=>{
                                            let selectedItem=userList?.find(d1 => d1.name === item.name);
                                            if(selectedItem) {
                                                setSelectedUser(selectedItem)
                                            }else {
                                                setSelectedUser('');
                                            }
                                        }}
                                        tooltipPosition={'left'}
                                        onSelectQueryChange={(text)=>{
                                            if(text){
                                                getUserListForReassign(text);
                                                setQuery(text);
                                            }else {
                                                getUserListForReassign('');
                                                setQuery('');
                                                setSelectedUser('');
                                            }
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                    </>
                }
            </div>
        }
    />
};

export default ReassignUser;