import {ConfirmationDialog, HeaderActions, Icons} from "../../../components";
import {useTranslation} from "react-i18next";
import {ReflexContainer, ReflexElement, ReflexSplitter} from "react-reflex";
import React, {useEffect, useState} from "react";
import styles from "./massRouteReassign.module.scss";
import {ICONS} from "../../../utils/iconNames";
import {Table} from "../../../components/tables/Table";
import {useDispatch, useSelector} from "react-redux";
import {deleteMassItem, getMassRouteList, getWorkItemStatusList} from "../../../api";
import {Button} from "@blueprintjs/core";
import _ from "lodash";
import {DATE_TIME_FORMAT, SORT_DIRECTION} from "../../../utils/common";
import {setMassRouteList, setWorkItemStatusList} from "../../../slices/dashboardSlice";
import moment from "moment";

const noSortColumns = ['statusIcon','status','workItemCount','searchTemplate'];
export const MassRouteReassignDialog = (props) => {
    const {t} = useTranslation();
    const dispatch = useDispatch();
    const { massRouteList , workItemStatusList, loading, workItemLoading }  = useSelector((state) => state.dashboard)
    const [tableData, setTableData] = useState([]);
    const [selectedRow, setSelectedRow] = useState([]);
    const [workItemListData, setWorkItemListData] = useState([]);
    const [selectedWorkStatusRow, setSelectedWorkStatusRow] = useState([]);
    const [showNoSortPopup, setShowNoSortPopup] = useState(false);
    const [showWorkItems, setShowWorkItems] = useState(true);
    const [showErrorDetails, setShowErrorDetails] = useState(true);
    const [isFirst, setIsFirst]=useState(true);
    const [jobStatusListLoading, setJobStatusListLoading]=useState(false);
    let parentHeight = window.screen.height > 1000 ? window.screen.height - 400 : window.screen.height - 300;

    const templateButtonActions = []
    templateButtonActions.push(<Button text={t('btn.searchbutton.view')} className={styles.buttonStyle} disabled={selectedWorkStatusRow?.length<=0} icon={<Icons icon={ICONS.DOC_VIEW}/>} style={{boxShadow: "none"}} onClick={() => {}}/>);
    templateButtonActions.push(<Button
        icon={showWorkItems ? <Icons icon={ICONS.ROUNDED_UP}/> : <Icons icon={ICONS.ROUNDED_DOWN}/>}
        style={{boxShadow: "none"}} onClick={() => {setShowWorkItems(!showWorkItems)}}/>);

    const templateButtonActions2 = [];
    templateButtonActions2.push(<Button
        icon={showErrorDetails ? <Icons icon={ICONS.ROUNDED_UP}/> : <Icons icon={ICONS.ROUNDED_DOWN}/>}
        style={{boxShadow: "none"}} onClick={() => {setShowErrorDetails(!showErrorDetails)}}/>);

    const [statusTable,setStatusTable]=useState([
        { name: 'statusIcon', caption: '', colWidth: 50},
        { name: 'status', caption: t('col_header_status'), colWidth: 110},
        { name: 'queue', caption: t('QUEUE_TO_CAPTION'), colWidth: 110},
        { name: 'newOwner', caption: t('REASSIGN_TO_CAPTION'), colWidth: 110},
        { name: 'reason', caption: t('label.hold.reason'), colWidth: 110},
        { name: 'workItemCount', caption: t('NUM_WORKITEM_CAPTION'), colWidth: 90},
        { name: 'action', caption: t('TYPE_TO_CAPTION'), colWidth: 110},
        { name: 'submitDate', caption: t('START_TIME_CAPTION'), colWidth: 110},
        { name: 'searchTemplate', caption: t('SEARCH_TO_CAPTION'), colWidth: 110}
    ]);

    const [workItemStatusTable,setWorkItemStatusTable]=useState([
        { name: 'statusIcon', caption: '', colWidth: 50},
        { name: 'status', caption: t('col_header_status'), colWidth: 110},
        { name: 'subject', caption: t('SUBJECT_TO_CAPTION'), colWidth: 110},
        { name: 'queue', caption: t('QUEUE_TO_CAPTION'), colWidth: 110},
        { name: 'originalQueue', caption: t('ORIG_QUEUE_TO_CAPTION'), colWidth: 110},
        { name: 'newUser', caption: t('NEW_USER_TO_CAPTION'), colWidth: 90},
        { name: 'originalUser', caption: t('ORIG_USER_TO_CAPTION'), colWidth: 110},
    ]);

    useEffect(()=>{
        setJobStatusListLoading(true)
        getMassRouteList();
        setTimeout(()=>setJobStatusListLoading(false),2000);
    },[]);

    useEffect(()=>{
        if(massRouteList.length) {
            let clone = [...massRouteList]
            let  arr=[];
            _.map(clone,(m)=>{
                arr.push({...m, statusIcon:<div style={{display:'flex', justifyContent:'center'}}>
                        {m.statusIcon == "<div class='ipdStateIcon ipdStateCompletedIcon' />" ?
                            <Icons icon={ICONS.STATE_OK}/> : <Icons icon={ICONS.STATE_FAILED}/>} </div>,
                        submitDate: moment(m.submitDate).format(DATE_TIME_FORMAT),
                        workItemCount: <div style={{display:'flex', justifyContent:'center'}}>{m.workItemCount}</div> })
            })
            isFirst && getWorkItemData(arr[0].id);
            setTableData(arr);
            setIsFirst(false);
        }
    },[massRouteList]);

    useEffect(()=>{
        if(workItemStatusList?.items?.length) {
            let clone = [...workItemStatusList?.items]
            let  arr=[];
            _.map(clone,(m)=>{
                arr.push({...m, statusIcon:<div style={{display:'flex', justifyContent:'center'}}>
                        {m.statusIcon == "<div class='ipdStateIcon ipdStateCompletedIcon' />" ?
                            <Icons icon={ICONS.STATE_OK}/> : <Icons icon={ICONS.STATE_FAILED}/>} </div>})
            })
            setWorkItemListData(arr);
        }
    },[workItemStatusList]);

    useEffect(()=>{
        if(selectedRow){
            getWorkItemData(selectedRow?.id);
        }
    },[selectedRow]);

    const getWorkItemData=async (id)=>{
        if(id){
            await getWorkItemStatusList(id);
        }
    }

    const onColumnSort=(fieldName, sortDir)=>{
        if(noSortColumns.includes(fieldName)){
            setShowNoSortPopup(!showNoSortPopup)
        }else {
            getMassRouteList(fieldName, sortDir === SORT_DIRECTION.ASC ? '+' : '-')
        }
    }

    const onWorkItemColumnSort=(fieldName, sortDir)=>{
        if(noSortColumns.includes(fieldName)){
            setShowNoSortPopup(!showNoSortPopup)
        }
        let sortedResult = _.orderBy([...workItemListData],fieldName,sortDir === SORT_DIRECTION.ASC?'asc':'desc')
        setWorkItemListData(sortedResult)
    }

    const handleRowSelect=(row)=>{
        setSelectedRow(row)
        setSelectedWorkStatusRow([])
    }

    const handleClose=()=>{
        props.setShowRouteReassignPopup();
        setSelectedWorkStatusRow([]);
        setSelectedRow(null)
        dispatch(setMassRouteList([]));
        dispatch(setWorkItemStatusList([]))
    }
    return <ConfirmationDialog
        width={1080}
        divideByY={18}
        divideByX={2}
        showDialog={true}
        headerText={`Ultera: ${t('com.ipd.process.workactions.status.header.title')}`}
        body={
            <div className={styles.parentDiv}>
                <div className={styles.subHeader} title={t('com.ipd.process.workactions.status.grid.title')}>
                        <div className={styles.headerLabelBox}>
                            <Icons icon={ICONS.WORK_FORWARD}/>
                            <div className={styles.headerText}>{t('com.ipd.process.workactions.status.grid.title')}</div>
                        </div>
                        <div>
                            <Button
                                icon={<Icons icon={ICONS.GRID_RESET} />}
                                text={t('com.ipd.process.jobs.status.refresh')}
                                title={t('com.ipd.process.jobs.status.refresh')}
                                onClick={()=> {
                                    setJobStatusListLoading(true)
                                    getMassRouteList();
                                    setSelectedWorkStatusRow([]);
                                    setTimeout(()=>setJobStatusListLoading(false),1000);
                                }}
                                className={styles.buttonStyle}/>
                            <Button
                                disabled={selectedRow?.status == t('ipd.cag.complaint.caserequirements.completed') || selectedRow?.status == undefined }
                                icon={<Icons icon={ICONS.GRID_REMOVE} />}
                                text={t('com.ipd.process.jobs.status.delete')}
                                title={t('com.ipd.process.jobs.status.delete')}
                                className={styles.buttonStyle}
                                onClick={()=>{
                                    let index=tableData.findIndex(d1 => d1.id === selectedRow.id)
                                    deleteMassItem(selectedRow?.id);
                                    setSelectedRow(tableData[index+1]);
                                    setSelectedWorkStatusRow([])
                                }}
                            />
                        </div>
                </div>
                <div className={styles.contentDiv}>
                    <ReflexContainer orientation="horizontal" style={{height: parentHeight}}>
                        <ReflexElement flex={3}>
                            <div className={styles.subContentDiv}>
                                <div className={styles.midContentBox}>
                                    <Table
                                        searchResultCol={statusTable}
                                        tableData={tableData}
                                        actionsOnTableRowClick={(row)=>handleRowSelect(row)}
                                        tableName={t('com.ipd.process.workactions.status.grid.title')}
                                        tableId={t('com.ipd.process.workactions.status.grid.title')}
                                        isSortable={true}
                                        setTableColumnsWidth={setStatusTable}
                                        onColumnSort={onColumnSort}
                                        noSortColumns={noSortColumns}
                                        loading={jobStatusListLoading}
                                        allowMultiRowSelection={false}
                                    />
                                </div>
                            </div>
                        </ReflexElement>
                        <ReflexSplitter className="horizontal-splitter"  onStartResize={()=> !showWorkItems && setShowWorkItems(!showWorkItems)}>
                            <div className="horizontal-splitter-thumb"/>
                        </ReflexSplitter>
                        {showWorkItems  ? <ReflexElement flex={2}  style={{overflow:"unset",minHeight:showWorkItems && '51px'}}>
                            <div style={{height: showWorkItems && "100%"}}
                                 className={styles.secondReflexDiv}>
                                <div className={styles.subHeader} style={{ height: 40}}>
                                    <div className={styles.headerLabelBox}>
                                        <Icons icon={ICONS.GRID_SHOW}/>
                                        <div className={styles.headerText}>{t('com.ipd.process.workactions.status.jobs.title')}</div>
                                    </div>
                                    <HeaderActions minimal={true} actions={templateButtonActions}/>
                                </div>
                                <div className={styles.detailsContent} style={{height: showWorkItems && "100%", overflow:'auto'}}>
                                    <div style={{height: showWorkItems && "100%"}}>
                                        <Table
                                            searchResultCol={workItemStatusTable}
                                            tableData={workItemListData}
                                            actionsOnTableRowClick={(item)=>setSelectedWorkStatusRow(item)}
                                            tableName={t('com.ipd.process.workactions.status.jobs.title')}
                                            tableId={t('com.ipd.process.workactions.status.jobs.title')}
                                            isSortable={true}
                                            setTableColumnsWidth={setWorkItemStatusTable}
                                            onColumnSort={onWorkItemColumnSort}
                                            noSortColumns={noSortColumns}
                                            loading={workItemLoading}
                                            noDataText={workItemListData.length ? '':t('case.reassign.status.selectRequestMsg')}
                                            allowMultiRowSelection={false}
                                        />
                                    </div>
                                </div>
                            </div>
                        </ReflexElement>:
                            <div className={styles.subHeader} style={{ height: 40}}>
                                <div className={styles.headerLabelBox}>
                                    <Icons icon={ICONS.GRID_SHOW}/>
                                    <div className={styles.headerText}>{t('com.ipd.process.workactions.status.jobs.title')}</div>
                                </div>
                                <HeaderActions minimal={true} actions={templateButtonActions}/>
                            </div>
                        }
                        <ReflexSplitter className="horizontal-splitter" onStartResize={()=>!showErrorDetails && setShowErrorDetails(!showErrorDetails)}>
                            <div className="horizontal-splitter-thumb"/>
                        </ReflexSplitter>
                        {showErrorDetails ? <ReflexElement flex={2}  style={{overflow:"unset"}}>
                            <div style={{height: showErrorDetails && "100%"}}
                                 className={styles.secondReflexDiv}>
                                <div className={styles.subHeader} style={{ height: 40}}>
                                    <div className={styles.headerLabelBox}>
                                        <Icons icon={ICONS.GRID_SHOW}/>
                                        <div className={styles.headerText} style={{color:'black'}}>{t('com.ipd.process.workactions.status.exceptions.title')}</div>
                                    </div>
                                    <HeaderActions minimal={true} actions={templateButtonActions2}/>
                                </div>
                                <div tabIndex={0} className={styles.errorDetailsContent} style={{height: showErrorDetails && "100%"}}>
                                    <label className={styles.detailsStyle} >{selectedWorkStatusRow?.message}</label>
                                </div>
                            </div>
                        </ReflexElement>:
                            <div className={styles.subHeader} style={{ height: 40}}>
                                <div className={styles.headerLabelBox}>
                                    <Icons icon={ICONS.GRID_SHOW}/>
                                    <div className={styles.headerText} style={{color:'black'}}>{t('com.ipd.process.workactions.status.exceptions.title')}</div>
                                </div>
                                <HeaderActions minimal={true} actions={templateButtonActions2}/>
                            </div>}
                    </ReflexContainer>
                       {
                           showNoSortPopup && <ConfirmationDialog
                               showDialog={true}
                               headerText={'Information'}
                               width={340}
                               divideByX={3.5}
                               divideByY={6}
                               icon={<Icons icon={ICONS.INFO}/>}
                               body={<div style={{height:'120px'}}>{t('admin_proceed_active_job_text')}</div>}
                               onClose={()=>setShowNoSortPopup(false)}
                               yesButtonText={t('btn.translationOk.ok')}
                               onYesBtnClick={()=>setShowNoSortPopup(false)}
                               isDismissOnEnterKey={true}
                               isCloseButtonShown={true}
                           />
                        }
                </div>
            </div>

        }
        transitionDuration={300}
        isCloseButtonShown={true}
        onClose={()=>handleClose()}
        noButtonText={t('btn.translationCancel.close')}
        onNoBtnClick={()=>handleClose()}
        tableData={tableData}
    />
}