import React, {useState} from 'react'
import {ContentPanel, ConfirmationDialog, HeaderActions, Icons, InputBox} from "../../../components";
import {ICONS} from "../../../utils/iconNames";
import {Button, Divider, H5} from "@blueprintjs/core";
import styles from "./systemManager.module.scss";
import {useTranslation} from "react-i18next";
import {INPUT_TYPE} from "../../../utils/common";

const configId='VSP85215ULT9QA';
const cacheConfigData=[{name:'Cached Enabled:',value:'true'},{name:'Refresh Rate (seconds):',value:'600'}]
const resultsSettingsConfigData=[
    {name:'Content Search Page Size',value:'23'},
    {name:'Case Search Page Size',value:'23'},
    {name:'Work Search Page Size',value:'23'},
    {name:'History Search Page Size',value:'23'}]

export const SystemManager  =(props)=>{
    const { t } = useTranslation();

    const [isDirty,setIsDirty]=useState(false);
    const [showDialog,setShowDialog]=useState(false);
    const [contentSearchError,setContentSearchError]=useState('');
    const [caseSearchError,setCaseSearchError]=useState('');
    const [workSearchError,setWorkSearchError]=useState('');
    const [historySearchError,setHistorySearchError]=useState('');

    const resultButtonActions = [];
    resultButtonActions.push(<Button onClick={()=>setShowDialog(true)} icon={ <Icons icon={ICONS.SAVE} />} text={t('toolseo.toolname.common.apply')} className={styles.buttonIcon} disabled={!isDirty}  />);
    resultButtonActions.push(<Button disabled={!isDirty} icon={ <Icons icon={ICONS.REFRESH} />} text={t('toolseo.toolname.common.undo')} className={styles.buttonIcon}/>);

    const systemManagerBody=()=>{
        const handleContentSearchTextChange=(e)=>{
            setIsDirty(true);
            if(e.target.value === ''){
                setContentSearchError(t('ipd.framework.validation.message.required'));
            }else if(e.target.value < 10 || e.target.value > 500){
                setContentSearchError(t('validation_resultSet_range'))
            }else{
                setContentSearchError('')
            }
        }
        const handleCaseSearchTextChange=(e)=>{
            setIsDirty(true);
            if(e.target.value === ''){
                setCaseSearchError(t('validation_required'));
            }else if(e.target.value < 10 || e.target.value > 500){
                setCaseSearchError(t('validation_resultSet_range'))
            }else{
                setCaseSearchError('')
            }
        }
        const handleWorkSearchTextChange=(e)=>{
            setIsDirty(true);
            if(e.target.value === ''){
                setWorkSearchError(t('validation_required'));
            }else if(e.target.value < 10 || e.target.value > 500){
                setWorkSearchError(t('validation_resultSet_range'))
            }else{
                setWorkSearchError('')
            }
        }
        const handleHistorySearchTextChange=(e)=>{
            setIsDirty(true);
            if(e.target.value === ''){
                setHistorySearchError(t('validation_required'));
            }else if(e.target.value < 10 || e.target.value > 500){
                setHistorySearchError(t('validation_resultSet_range'))
            }else{
                setHistorySearchError('')
            }
        }

        const renderResultSettingsColumns=(d)=>{
                 return <>
                     <div className={styles.resultSettingsGrid}>
                         <div className={styles.resultSettingsCol1}>
                             <H5 className={styles.requiredField}>{'*'}</H5>
                             <h4 className={styles.textLabel}>{d[0].name}</h4>
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 value={d[0].value}
                                 isDisabled={true}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 inputType={INPUT_TYPE.NUMBER}
                                 isDisabled={false}
                                 onChange={(e)=>handleContentSearchTextChange(e)}
                                 errorText={contentSearchError}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                     </div>
                     <div className={styles.resultSettingsGrid}>
                         <div className={styles.resultSettingsCol1}>
                             <H5 className={styles.requiredField}>{'*'}</H5>
                             <h4 className={styles.textLabel}>{d[1].name}</h4>
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 value={d[1].value}
                                 isDisabled={true}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 inputType={INPUT_TYPE.PHONE}
                                 isDisabled={false}
                                 onChange={(e)=>handleCaseSearchTextChange(e)}
                                 errorText={caseSearchError}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                     </div>
                     <div className={styles.resultSettingsGrid}>
                         <div className={styles.resultSettingsCol1}>
                             <H5 className={styles.requiredField}>{'*'}</H5>
                             <h4 className={styles.textLabel}>{d[2].name}</h4>
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 value={d[2].value}
                                 isDisabled={true}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 inputType={INPUT_TYPE.PHONE}
                                 isDisabled={false}
                                 onChange={(e)=>handleWorkSearchTextChange(e)}
                                 errorText={workSearchError}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                     </div>
                     <div className={styles.resultSettingsGrid}>
                         <div className={styles.resultSettingsCol1}>
                             <H5 className={styles.requiredField}>{'*'}</H5>
                             <h4 className={styles.textLabel}>{d[3].name}</h4>
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 value={d[3].value}
                                 isDisabled={true}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                         <div className={styles.resultSettingsGridContainer}>
                             <InputBox
                                 inputType={INPUT_TYPE.PHONE}
                                 isDisabled={false}
                                 onChange={(e)=>handleHistorySearchTextChange(e)}
                                 errorText={historySearchError}
                                 height={'17px'}
                                 width={'135px'}
                             />
                         </div>
                     </div>

                     </>
        }

        const renderCacheSettingsColumns=(data)=> {
            return data.map((d,index) => {
                return <div key={index} className={styles.resultSettingsGrid}>
                    <div className={styles.cacheResName}><h4>{d.name}</h4></div>
                    <div className={styles.cacheResValue}><h4 style={{fontWeight:'100',lineHeight:'0px'}}>{d.value}</h4></div>
                </div>

            });
        }

        return <div className={styles.container}>
            <div className={styles.headerContainer}><H5 className={styles.cardHeader}>{t('sysmgr.context.cachesettings.title')}</H5></div>
            <div className={styles.cardContainer}>
                <div className={styles.cacheConfigSubContainer}>
                    {renderCacheSettingsColumns(cacheConfigData)}
                </div>
            </div>

            <div className={styles.headerContainer}><H5 className={styles.cardHeader}>{t('sysmgr.context.resultssettings.title')}</H5></div>
            <div className={styles.cardContainer}>
                <div className={styles.resultSettingsContainer}>
                    <div className={styles.resultSettingsHeaderContainer}>
                        <div className={styles.resultSettingsGridContainer}>
                            <div className={styles.resultSettingsGrid}>
                                <div className={styles.resultSettingsCol1}/>
                                <div className={styles.resultSettingsGridContainer}> <h4 className={styles.textLabel}>{t('label_runtime')}</h4></div>
                                <div className={styles.resultSettingsGridContainer}> <h4 className={styles.textLabel}>{t('label_configured')}</h4></div>
                            </div>
                        </div>
                        <div className={styles.resultSettingsRightSideDetails}/>
                    </div>
                    <div className={styles.resultSettingsGrid}>
                        <div className={styles.resultSettingsGridContainer}>
                            {renderResultSettingsColumns(resultsSettingsConfigData)}
                        </div>
                        <div className={styles.resultSettingsRightSideDetails}>
                            <h4 className={styles.resultSettingsRightSideText}>{t('sysmgr.context.restartMessage')}</h4>
                        </div>
                    </div>
                </div>
            </div>

            <div className={styles.headerContainer}><H5 className={styles.cardHeader}>{t('sysmgr.context.licenseinfo.title')}</H5></div>
            <div className={styles.cardContainer}>
                <div className={styles.licenceInfoContainer}>
                    <Icons icon={ICONS.COMMON_ALARM}/>
                    <h3 className={styles.licenceInfoText}>{t('sysmgr.context.licenseinfo.msg')}</h3>
                </div>
            </div>
        </div>
    }

    const handleDialog=()=>{
        setShowDialog(!showDialog);
    }

    return(
        <>
            <ContentPanel
                headerIcon= {<Icons icon={ICONS.MODULE_PREFERENCES}/>}
                headerTitle={t('label.bootstrap.ceConfiguration') +configId}
                actions={<HeaderActions minimal={true} actions={resultButtonActions}/>}
                body={ systemManagerBody() }
            />
            <ConfirmationDialog
                showDialog={showDialog}
                icon={<div className={styles.loadingGifStyle}/>}
                headerText={t('label_progress')}
                onClose={handleDialog}
                body={<h4 className={styles.licenceInfoText}>{t('message_applySystemManagerChanges')}</h4>}
                onNoBtnClick={handleDialog}
                noButtonText={t('btn.common.cancel')}
                isCloseButtonShown={true}/>
        </>
    )
}